import React, { useState, useEffect } from 'react';
import { HashRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { ThemeProvider, CssBaseline } from '@mui/material';
import { lightTheme, darkTheme } from './styles/theme';
import Login from './components/Login';
import ChatInterface from './components/ChatInterface';
import EnhancedChatInterface from './components/EnhancedChatInterface';
import DebugPanel from './components/DebugPanel';
import './styles/globalStyles.css';

const ProtectedRoute = ({ children }) => {
  const isAuthenticated = localStorage.getItem('isAuthenticated') === 'true';
  return isAuthenticated ? children : <Navigate to="/" />;
};

function App() {
  const [mode, setMode] = useState('light');
  
  // Check for saved theme preference
  useEffect(() => {
    const savedMode = localStorage.getItem('themeMode');
    if (savedMode) {
      setMode(savedMode);
    } else if (window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches) {
      // Use dark mode if user prefers dark mode
      setMode('dark');
    }
  }, []);
  
  const toggleTheme = () => {
    const newMode = mode === 'light' ? 'dark' : 'light';
    setMode(newMode);
    localStorage.setItem('themeMode', newMode);
    
    // Add or remove the dark-mode class from the body
    if (newMode === 'dark') {
      document.body.classList.add('dark-mode');
    } else {
      document.body.classList.remove('dark-mode');
    }
  };
  
  return (
    <ThemeProvider theme={mode === 'light' ? lightTheme : darkTheme}>
      <CssBaseline />
      <Router basename='/'>
        <Routes>
          <Route path="/" element={<Login />} />
          <Route 
            path="/chat" 
            element={
              <ProtectedRoute>
                <EnhancedChatInterface toggleTheme={toggleTheme} mode={mode} />
              </ProtectedRoute>
            } 
          />
          {/* Add this route to handle Microsoft redirects */}
          <Route path="*" element={<Login />} />
        </Routes>
      </Router>
      {/* Debug panel only appears in development mode */}
      {process.env.NODE_ENV === 'development' && <DebugPanel />}
    </ThemeProvider>
  );
}

export default App;
