import React, { useState, useEffect } from 'react';
import { login } from './api';
import { useNavigate } from 'react-router-dom';
import { 
  Box, 
  Button, 
  TextField, 
  Typography, 
  Paper, 
  Container, 
  CircularProgress,
  Snackbar,
  Alert,
  InputAdornment,
  IconButton,
  Divider
} from '@mui/material';
import { Visibility, VisibilityOff, LockOutlined } from '@mui/icons-material';

const Login = () => {
  const navigate = useNavigate();
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [loading, setLoading] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const [error, setError] = useState('');
  const [msalInstance, setMsalInstance] = useState(null);
  const [msalLoading, setMsalLoading] = useState(false);
  const [usingMsalFallback, setUsingMsalFallback] = useState(false);
  
  // Debug logging for authentication flow
  useEffect(() => {
    console.log('Current URL:', window.location.href);
    console.log('Origin:', window.location.origin);
    
    const urlParams = new URLSearchParams(window.location.search);
    const codeParam = urlParams.get('code');
    if (codeParam) {
      console.log('Auth code found in URL parameters');
    }
    
    const hashParams = new URLSearchParams(window.location.hash.replace('#', '?'));
    const idToken = hashParams.get('id_token');
    if (idToken) {
      console.log('ID token found in hash fragment');
    }
  }, []);
  
  // Load the MSAL script dynamically
  useEffect(() => {
    const loadMsalScript = async () => {
      if (window.msal) return;
      
      const script = document.createElement('script');
      script.src = 'https://alcdn.msauth.net/browser/2.30.0/js/msal-browser.min.js';
      script.async = true;
      script.onload = initializeMsal;
      
      document.body.appendChild(script);
    };
    
    loadMsalScript();
  }, []);
  
  // Initialize MSAL
  const initializeMsal = () => {
    if (window.msal) {
      // Fixed redirect URI that exactly matches what's in Azure
      const redirectUri = 'https://dmos82.github.io/goldkey-chat-app/';
      
      // For debugging
      console.log('MSAL initialization with:', {
        clientId: '62d2bc0f-d2ce-4592-811f-d042f2ccae81',
        redirectUri: redirectUri
      });
      
      const msalConfig = {
        auth: {
          clientId: '62d2bc0f-d2ce-4592-811f-d042f2ccae81',
          authority: 'https://login.microsoftonline.com/f41e7940-081f-471f-bc04-f17f7eff39a7',
          redirectUri: redirectUri,
          navigateToLoginRequestUrl: true,
          postLogoutRedirectUri: redirectUri
        },
        cache: {
          cacheLocation: 'localStorage',
          storeAuthStateInCookie: true,
        },
        system: {
          loggerOptions: {
            loggerCallback: (level, message, containsPii) => {
              if (!containsPii) console.log("[MSAL]", message);
            },
            piiLoggingEnabled: false,
            logLevel: 3 // Verbose logging
          }
        }
      };
      
      try {
        const msalInstance = new window.msal.PublicClientApplication(msalConfig);
        setMsalInstance(msalInstance);
        
        // Check for existing accounts
        const accounts = msalInstance.getAllAccounts();
        if (accounts.length > 0) {
          console.log('Found existing accounts:', accounts.length);
          handleResponse({ account: accounts[0] });
        } else {
          // Check for response from redirect
          msalInstance.handleRedirectPromise()
            .then(response => {
              console.log('Redirect promise resolved', response);
              handleResponse(response);
            })
            .catch(err => {
              console.error('Redirect promise error:', err);
              setError('Microsoft authentication failed. Please try again or use standard login.');
            });
        }
      } catch (error) {
        console.error('MSAL initialization error:', error);
        setUsingMsalFallback(true);
      }
    }
  };
  
  // Handle MSAL response
  const handleResponse = (response) => {
    console.log('Handling MSAL response:', response);
    
    if (response && response.account) {
      // User has been authenticated, save their info
      const user = {
        name: response.account.name || response.account.username || 'Staff Member',
        email: response.account.username || 'staff@goldkey.com',
        role: 'staff'
      };
      
      localStorage.setItem('isAuthenticated', 'true');
      localStorage.setItem('user', JSON.stringify(user));
      localStorage.setItem('msalAccount', JSON.stringify(response.account));
      
      navigate('/chat');
    }
  };
  
  // Sign in with Microsoft
  const signInWithMicrosoft = () => {
    if (!msalInstance) {
      console.error('MSAL instance not available');
      setError('Microsoft authentication is not available. Please try again later.');
      setUsingMsalFallback(true);
      return;
    }
    
    setMsalLoading(true);
    
    try {
      const loginRequest = {
        scopes: ['openid', 'profile', 'User.Read'],
      };
      
      console.log('Attempting MSAL login with scopes:', loginRequest.scopes);
      
      // Prefer redirect method for better compatibility with GitHub Pages
      msalInstance.loginRedirect(loginRequest)
        .catch(error => {
          console.error('MSAL Redirect Error:', error);
          
          // Fall back to popup if redirect fails
          msalInstance.loginPopup(loginRequest)
            .then(response => {
              console.log('MSAL popup login success:', response);
              handleResponse(response);
              setMsalLoading(false);
            })
            .catch(error => {
              console.error('MSAL Popup Error:', error);
              
              // Switch to fallback mode
              setUsingMsalFallback(true);
              setError('Microsoft authentication failed. Using demo login instead.');
              setMsalLoading(false);
            });
        });
    } catch (error) {
      console.error('MSAL Exception:', error);
      
      // Switch to fallback mode
      setUsingMsalFallback(true);
      setError('Microsoft authentication failed. Using demo login instead.');
      setMsalLoading(false);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!username.trim() || !password.trim()) {
      setError('Please enter both username and password');
      return;
    }
    
    setLoading(true);
    
    try {
      const response = await login(username, password);
      
      // Store authentication data
      localStorage.setItem('isAuthenticated', 'true');
      localStorage.setItem('user', JSON.stringify(response.user));
      if (response.token) {
        localStorage.setItem('token', response.token);
      }
      
      navigate('/chat');
    } catch (error) {
      setError(error.message || 'Authentication failed');
      setLoading(false);
    }
  };

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setError('');
  };

  return (
    <Container component="main" maxWidth="xs">
      <Paper 
        elevation={3} 
        sx={{ 
          p: 4, 
          display: 'flex', 
          flexDirection: 'column', 
          alignItems: 'center',
          mt: 8,
          borderRadius: 2,
          bgcolor: 'background.paper'
        }}
      >
        <Box 
          sx={{ 
            display: 'flex', 
            flexDirection: 'column', 
            alignItems: 'center',
            width: '100%'
          }}
        >
          <Box 
            component="img" 
            src="/goldkey-chat-app/goldkey-logo.svg" 
            alt="GoldKey Insurance" 
            sx={{ 
              height: 80, 
              mb: 2 
            }} 
          />
          
          <Typography component="h1" variant="h5" sx={{ mb: 3, color: 'primary.main', fontWeight: 'bold' }}>
            Insurance Assistant
          </Typography>
          
          {/* Microsoft/Outlook login button */}
          <Button
            fullWidth
            variant="outlined"
            startIcon={<LockOutlined />}
            onClick={() => {
              if (usingMsalFallback) {
                // Use fallback if we already tried MSAL
                setUsername('outlook@goldkey.com');
                setPassword('goldkey');
                setTimeout(() => {
                  const fakeEvent = { preventDefault: () => {} };
                  handleSubmit(fakeEvent);
                }, 1000);
              } else {
                // Try real Microsoft auth first
                signInWithMicrosoft();
              }
            }}
            sx={{ 
              mb: 2, 
              py: 1.5,
              color: '#0078d4',
              borderColor: '#0078d4',
              '&:hover': {
                borderColor: '#106ebe',
                backgroundColor: 'rgba(0, 120, 212, 0.04)'
              }
            }}
          >
            {msalLoading ? 
              <CircularProgress size={24} /> : 
              usingMsalFallback ? 'Sign in with Outlook (Demo)' : 'Sign in with Microsoft'}
          </Button>
          
          <Divider sx={{ width: '100%', my: 2 }}>OR</Divider>
          
          <Box component="form" onSubmit={handleSubmit} sx={{ width: '100%' }}>
            <TextField
              margin="normal"
              required
              fullWidth
              id="username"
              label="Username"
              name="username"
              autoComplete="username"
              autoFocus
              value={username}
              onChange={(e) => setUsername(e.target.value)}
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="password"
              label="Password"
              type={showPassword ? 'text' : 'password'}
              id="password"
              autoComplete="current-password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end">
                    <IconButton
                      aria-label="toggle password visibility"
                      onClick={() => setShowPassword(!showPassword)}
                      edge="end"
                    >
                      {showPassword ? <VisibilityOff /> : <Visibility />}
                    </IconButton>
                  </InputAdornment>
                ),
              }}
            />
            <Button
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
              sx={{ mt: 3, mb: 2, py: 1.5 }}
              disabled={loading}
            >
              {loading ? <CircularProgress size={24} /> : 'Sign In'}
            </Button>
          </Box>
          
          <Typography variant="body2" color="text.secondary" align="center" sx={{ mt: 2 }}>
            Staff Access Only  GoldKey Insurance
          </Typography>
        </Box>
      </Paper>
      
      <Snackbar open={!!error} autoHideDuration={6000} onClose={handleClose}>
        <Alert onClose={handleClose} severity="error" sx={{ width: '100%' }}>
          {error}
        </Alert>
      </Snackbar>
    </Container>
  );
};

export default Login;
