import React, { useState, useEffect, useRef } from 'react';
import { 
  Box, 
  Paper, 
  TextField, 
  IconButton, 
  Typography, 
  CircularProgress,
  Divider,
  Avatar,
  Tooltip,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  Menu,
  MenuItem,
  AppBar,
  Toolbar,
  useTheme,
  useMediaQuery,
  Chip,
  Collapse,
  LinearProgress
} from '@mui/material';
import {
  Send,
  CloudUpload,
  QuestionAnswer,
  Star,
  StarBorder,
  MoreVert,
  Logout,
  NightsStay,
  LightMode,
  AccountCircle,
  FilterList,
  History,
  Policy,
  Person,
  Help,
  Download,
  Assignment,
  Error as ErrorIcon,
  NetworkCheck,
  Add,
  ExpandMore,
  ExpandLess
} from '@mui/icons-material';
import { sendMessage, uploadDocument, checkServiceStatus, saveFavoriteQuery, getUserFavorites } from './enhancedApi';
import { processQuery, generateFollowUpQuestions } from '../utils/queryProcessor';

// Message component to display individual messages
const Message = ({ message, isUser, onFollowUpClick }) => {
  const theme = useTheme();
  const [expanded, setExpanded] = useState(false);
  
  // Display follow-up suggestions if they exist and this is an AI message
  const hasFollowUps = !isUser && message.followUpSuggestions && message.followUpSuggestions.length > 0;
  
  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: isUser ? 'flex-end' : 'flex-start',
        mb: 2,
        maxWidth: '100%',
      }}
    >
      <Box
        sx={{
          display: 'flex',
          justifyContent: isUser ? 'flex-end' : 'flex-start',
          width: '100%',
        }}
      >
        {!isUser && (
          <Avatar 
            sx={{ 
              bgcolor: 'secondary.main', 
              mr: 1,
              width: 38, 
              height: 38,
              flexShrink: 0
            }}
          >
            GK
          </Avatar>
        )}
        
        <Paper
          elevation={1}
          sx={{
            p: 2,
            maxWidth: '75%',
            borderRadius: 2,
            bgcolor: isUser ? 'primary.main' : 'background.paper',
            color: isUser ? 'white' : 'text.primary',
            border: isUser ? 'none' : `1px solid ${theme.palette.divider}`,
          }}
        >
          <Typography variant="body1" sx={{ wordBreak: 'break-word' }}>{message.text}</Typography>
          
          {message.timestamp && (
            <Typography 
              variant="caption" 
              sx={{ 
                display: 'block', 
                mt: 1, 
                color: isUser ? 'rgba(255,255,255,0.7)' : 'text.secondary',
                textAlign: 'right'
              }}
            >
              {new Date(message.timestamp).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
            </Typography>
          )}
        </Paper>
        
        {isUser && (
          <Avatar 
            sx={{ 
              bgcolor: 'primary.light', 
              ml: 1,
              width: 38, 
              height: 38,
              flexShrink: 0
            }}
          >
            <AccountCircle />
          </Avatar>
        )}
      </Box>
      
      {/* Follow-up suggestions */}
      {hasFollowUps && (
        <Box 
          sx={{ 
            ml: 7, 
            mt: 1, 
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'flex-start'
          }}
        >
          <Button
            size="small"
            onClick={() => setExpanded(!expanded)}
            startIcon={expanded ? <ExpandLess /> : <ExpandMore />}
            sx={{ mb: 1, textTransform: 'none', color: 'text.secondary' }}
          >
            {expanded ? 'Hide suggestions' : 'Show suggested follow-ups'}
          </Button>
          
          <Collapse in={expanded} timeout="auto" unmountOnExit sx={{ width: '100%' }}>
            <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1, mb: 1 }}>
              {message.followUpSuggestions.map((suggestion, index) => (
                <Chip
                  key={index}
                  label={suggestion}
                  onClick={() => onFollowUpClick(suggestion)}
                  clickable
                  color="primary"
                  variant="outlined"
                  size="small"
                />
              ))}
            </Box>
          </Collapse>
        </Box>
      )}
    </Box>
  );
};

// Quick action button component
const QuickActionButton = ({ icon, label, onClick }) => {
  return (
    <Button
      variant="outlined"
      color="primary"
      size="small"
      startIcon={icon}
      onClick={onClick}
      sx={{ 
        mr: 1, 
        mb: 1, 
        borderRadius: 4,
        px: 2,
        py: 1,
        textTransform: 'none'
      }}
    >
      {label}
    </Button>
  );
};

const EnhancedChatInterface = ({ toggleTheme, mode }) => {
  const [input, setInput] = useState('');
  const [messages, setMessages] = useState([]);
  const [loading, setLoading] = useState(false);
  const [favorites, setFavorites] = useState([]);
  const [openFavorites, setOpenFavorites] = useState(false);
  const [uploadDialog, setUploadDialog] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);
  const [selectedFile, setSelectedFile] = useState(null);
  const [serviceOnline, setServiceOnline] = useState(true);
  const [uploadProgress, setUploadProgress] = useState(0);
  
  const messagesEndRef = useRef(null);
  const fileInputRef = useRef(null);
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  
  // User info from authentication
  const user = JSON.parse(localStorage.getItem('user')) || { name: 'Staff Member' };
  
  // Sample quick actions using real insurance queries
  const quickActions = [
    { icon: <Policy />, label: 'Policy Coverage', query: 'What does policy GK-1234 cover?' },
    { icon: <Person />, label: 'Client Info', query: 'Who is our newest client?' },
    { icon: <Assignment />, label: 'Claim Status', query: 'What is the status of claim C-5678?' },
    { icon: <Help />, label: 'FAQ', query: 'What are our office hours?' },
  ];

  // Initialize chat and check service status
  useEffect(() => {
    // Welcome message
    setMessages([
      {
        id: 1,
        text: `Hello ${user.name}! How can I assist you with GoldKey Insurance information today?`,
        timestamp: new Date(),
        isUser: false,
        followUpSuggestions: [
          "Show me policies expiring this month",
          "What's our newest client's info?",
          "List open claims awaiting approval"
        ]
      },
    ]);
    
    // Load favorites
    loadFavorites();
    
    // Check if the backend service is online
    const checkService = async () => {
      const isOnline = await checkServiceStatus();
      setServiceOnline(isOnline);
    };
    
    checkService();
    
    // Set up periodic service checks
    const interval = setInterval(checkService, 60000); // Check every minute
    
    return () => clearInterval(interval);
  }, []);

  // Load user favorites
  const loadFavorites = async () => {
    try {
      const userFavorites = await getUserFavorites(user.id);
      setFavorites(userFavorites);
    } catch (error) {
      console.error('Error loading favorites:', error);
      // Fall back to sample favorites
      setFavorites([
        'List all policies expiring this month',
        'Who is the agent for client Johnson?',
        'What is the status of claim #C-4921?',
        'Show me policy renewal rates'
      ]);
    }
  };

  // Scroll to bottom when messages change
  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  // Process a message and get AI response
  const handleSend = async () => {
    if (!input.trim()) return;
    
    const userMessage = {
      id: Date.now(),
      text: input,
      timestamp: new Date(),
      isUser: true,
    };
    
    setMessages((prev) => [...prev, userMessage]);
    setInput('');
    setLoading(true);
    
    try {
      // Use the query processor to enrich the message
      const processedQuery = processQuery(input, user.name);
      
      // Call the enhanced API to send the message
      const response = await sendMessage(input, user.name, user.id);
      
      // Generate follow-up suggestions based on the query and response
      const followUps = generateFollowUpQuestions(
        input, 
        processedQuery.intent, 
        processedQuery.entities
      );
      
      const aiResponse = {
        id: Date.now() + 1,
        text: response.message,
        timestamp: new Date(),
        isUser: false,
        followUpSuggestions: followUps
      };
      
      setMessages((prev) => [...prev, aiResponse]);
      
      // Update service status based on successful response
      if (!serviceOnline) setServiceOnline(true);
    } catch (error) {
      console.error('Error sending message:', error);
      setLoading(false);
      
      // If service is down, update status
      if (error.message?.includes('No response from server')) {
        setServiceOnline(false);
      }
      
      // Add error message
      setMessages((prev) => [
        ...prev,
        {
          id: Date.now() + 1,
          text: error.friendlyMessage || 'Sorry, there was an error processing your request. Please try again.',
          timestamp: new Date(),
          isUser: false,
          error: true
        },
      ]);
    } finally {
      setLoading(false);
    }
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSend();
    }
  };

  const handleQuickAction = (query) => {
    setInput(query);
  };

  const handleFollowUpClick = (query) => {
    setInput(query);
    // Optional: auto-send the follow-up
    // setTimeout(handleSend, 100);
  };

  const handleMenuClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  const handleLogout = () => {
    localStorage.removeItem('isAuthenticated');
    localStorage.removeItem('user');
    localStorage.removeItem('token');
    localStorage.removeItem('msalAccount');
    window.location.href = '/';
  };

  const handleAddToFavorites = async () => {
    if (!input.trim()) return;
    
    try {
      const updatedFavorites = await saveFavoriteQuery(user.id, input);
      setFavorites(updatedFavorites);
      setInput('');
      handleMenuClose();
    } catch (error) {
      console.error('Error saving favorite:', error);
      // Stay silent on error, keep the UI flowing
    }
  };

  const handleFileUpload = (event) => {
    const file = event.target.files[0];
    if (!file) return;
    
    setSelectedFile(file);
    setUploadProgress(0);
    setUploadDialog(true);
  };

  const handleUploadConfirm = async () => {
    if (!selectedFile) return;
    
    setMessages((prev) => [
      ...prev,
      {
        id: Date.now(),
        text: `Uploading document: ${selectedFile.name}`,
        timestamp: new Date(),
        isUser: true,
      },
    ]);
    
    setLoading(true);
    setUploadDialog(false);
    
    try {
      // Upload file with progress tracking
      const updateProgress = (progress) => {
        setUploadProgress(progress);
      };
      
      const response = await uploadDocument(selectedFile, user.name, user.id, updateProgress);
      
      setMessages((prev) => [
        ...prev,
        {
          id: Date.now() + 1,
          text: response.message,
          timestamp: new Date(),
          isUser: false,
        },
      ]);
    } catch (error) {
      setMessages((prev) => [
        ...prev,
        {
          id: Date.now() + 1,
          text: `Error uploading document: ${error.friendlyMessage || error.message}`,
          timestamp: new Date(),
          isUser: false,
          error: true
        },
      ]);
    } finally {
      setLoading(false);
      setUploadProgress(0);
      setSelectedFile(null);
    }
  };

  const exportConversation = () => {
    const exportData = messages.map(msg => ({
      sender: msg.isUser ? user.name : 'GoldKey Assistant',
      text: msg.text,
      time: new Date(msg.timestamp).toLocaleString()
    }));
    
    const exportText = exportData.map(item => 
      `[${item.time}] ${item.sender}: ${item.text}`
    ).join('\n\n');
    
    const blob = new Blob([exportText], { type: 'text/plain' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `GoldKey-Chat-${new Date().toISOString().split('T')[0]}.txt`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
    
    handleMenuClose();
  };

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', height: '100vh' }}>
      {/* Header */}
      <AppBar position="static" color="primary">
        <Toolbar>
          <Box
            component="img"
            src="/goldkey-chat-app/goldkey-logo.svg"
            alt="GoldKey Insurance"
            sx={{
              height: 40,
              mr: 1,
              display: { xs: 'none', sm: 'block' }
            }}
          />
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            GoldKey Insurance Assistant
          </Typography>
          
          {/* Service status indicator */}
          <Tooltip title={serviceOnline ? "Service Online" : "Service Offline"}>
            <IconButton color="inherit" size="small" sx={{ mr: 1 }}>
              {serviceOnline ? 
                <NetworkCheck color="inherit" /> : 
                <ErrorIcon sx={{ color: 'error.light' }} />
              }
            </IconButton>
          </Tooltip>
          
          <Tooltip title="Toggle dark/light mode">
            <IconButton color="inherit" onClick={toggleTheme}>
              {mode === 'dark' ? <LightMode /> : <NightsStay />}
            </IconButton>
          </Tooltip>
          
          <IconButton
            color="inherit"
            edge="end"
            aria-label="menu"
            onClick={handleMenuClick}
          >
            <MoreVert />
          </IconButton>
          
          <Menu
            anchorEl={anchorEl}
            open={Boolean(anchorEl)}
            onClose={handleMenuClose}
          >
            <MenuItem onClick={() => {
              setOpenFavorites(true);
              handleMenuClose();
            }}>
              <ListItemIcon>
                <Star fontSize="small" />
              </ListItemIcon>
              <ListItemText>Saved Questions</ListItemText>
            </MenuItem>
            
            <MenuItem onClick={handleAddToFavorites} disabled={!input.trim()}>
              <ListItemIcon>
                <StarBorder fontSize="small" />
              </ListItemIcon>
              <ListItemText>Save Current Question</ListItemText>
            </MenuItem>
            
            <MenuItem onClick={exportConversation}>
              <ListItemIcon>
                <Download fontSize="small" />
              </ListItemIcon>
              <ListItemText>Export Conversation</ListItemText>
            </MenuItem>
            
            <Divider />
            
            <MenuItem onClick={handleLogout}>
              <ListItemIcon>
                <Logout fontSize="small" />
              </ListItemIcon>
              <ListItemText>Logout</ListItemText>
            </MenuItem>
          </Menu>
        </Toolbar>
      </AppBar>

      {/* Service status alert */}
      {!serviceOnline && (
        <Paper 
          elevation={0} 
          sx={{ 
            bgcolor: 'warning.light', 
            color: 'warning.contrastText',
            py: 1,
            px: 2,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center'
          }}
        >
          <ErrorIcon sx={{ mr: 1, fontSize: 20 }} />
          <Typography variant="body2">
            Service is currently offline. Using fallback mode.
          </Typography>
        </Paper>
      )}

      {/* Chat Messages Area */}
      <Box
        sx={{
          flexGrow: 1,
          overflow: 'auto',
          p: 2,
          bgcolor: 'background.default',
        }}
      >
        {messages.map((message) => (
          <Message
            key={message.id}
            message={message}
            isUser={message.isUser}
            onFollowUpClick={handleFollowUpClick}
          />
        ))}
        
        {loading && (
          <Box sx={{ display: 'flex', justifyContent: 'center', mt: 2 }}>
            <CircularProgress color="secondary" size={40} />
          </Box>
        )}
        
        {uploadProgress > 0 && uploadProgress < 100 && (
          <Box sx={{ width: '70%', mt: 2, mx: 'auto' }}>
            <Typography variant="body2" color="text.secondary" sx={{ mb: 1 }}>
              Uploading document: {Math.round(uploadProgress)}%
            </Typography>
            <LinearProgress variant="determinate" value={uploadProgress} />
          </Box>
        )}
        
        <div ref={messagesEndRef} />
      </Box>

      {/* Quick Action Buttons */}
      <Box 
        sx={{ 
          p: 2, 
          borderTop: 1, 
          borderColor: 'divider',
          bgcolor: 'background.paper',
          display: 'flex',
          flexWrap: 'wrap',
          justifyContent: 'flex-start'
        }}
      >
        {quickActions.map((action, index) => (
          <QuickActionButton
            key={index}
            icon={action.icon}
            label={action.label}
            onClick={() => handleQuickAction(action.query)}
          />
        ))}
      </Box>

      {/* Input Area */}
      <Paper
        elevation={3}
        sx={{
          p: 2,
          bgcolor: 'background.paper',
          borderTop: 1,
          borderColor: 'divider',
        }}
      >
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          <input
            type="file"
            ref={fileInputRef}
            onChange={handleFileUpload}
            style={{ display: 'none' }}
            accept=".pdf,.doc,.docx,.txt"
          />
          
          <Tooltip title="Upload Document">
            <IconButton
              color="primary"
              onClick={() => fileInputRef.current.click()}
              sx={{ mr: 1 }}
            >
              <CloudUpload />
            </IconButton>
          </Tooltip>
          
          <TextField
            fullWidth
            variant="outlined"
            placeholder="Ask about policies, claims, or clients..."
            value={input}
            onChange={(e) => setInput(e.target.value)}
            onKeyPress={handleKeyPress}
            sx={{ 
              '& .MuiOutlinedInput-root': {
                borderRadius: 2,
              }
            }}
          />
          
          <Tooltip title="Send Message">
            <IconButton
              color="primary"
              onClick={handleSend}
              disabled={!input.trim() || loading}
              sx={{ ml: 1 }}
            >
              <Send />
            </IconButton>
          </Tooltip>
        </Box>
      </Paper>

      {/* Favorites Dialog */}
      <Dialog
        open={openFavorites}
        onClose={() => setOpenFavorites(false)}
        maxWidth="sm"
        fullWidth
      >
        <DialogTitle>
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <Star color="secondary" sx={{ mr: 1 }} />
            Saved Questions
          </Box>
        </DialogTitle>
        
        <DialogContent>
          <List>
            {favorites.map((favorite, index) => (
              <ListItem 
                key={index}
                button
                onClick={() => {
                  setInput(favorite);
                  setOpenFavorites(false);
                }}
              >
                <ListItemIcon>
                  <QuestionAnswer color="primary" />
                </ListItemIcon>
                <ListItemText primary={favorite} />
              </ListItem>
            ))}
          </List>
        </DialogContent>
        
        <DialogActions>
          <Button onClick={() => setOpenFavorites(false)}>Close</Button>
        </DialogActions>
      </Dialog>

      {/* File Upload Dialog */}
      <Dialog
        open={uploadDialog}
        onClose={() => setUploadDialog(false)}
      >
        <DialogTitle>Upload Document</DialogTitle>
        
        <DialogContent>
          <Typography variant="body1">
            {selectedFile ? (
              <>
                Selected file: <strong>{selectedFile.name}</strong> ({Math.round(selectedFile.size / 1024)} KB)
              </>
            ) : (
              'No file selected'
            )}
          </Typography>
          
          <Typography variant="body2" color="text.secondary" sx={{ mt: 2 }}>
            This document will be processed by the GoldKey AI Assistant for analysis.
          </Typography>
        </DialogContent>
        
        <DialogActions>
          <Button onClick={() => setUploadDialog(false)}>Cancel</Button>
          <Button 
            variant="contained" 
            color="primary" 
            onClick={handleUploadConfirm}
            disabled={!selectedFile}
          >
            Upload
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default EnhancedChatInterface;
