import axios from 'axios';

// Get API URL from environment variables with fallback
const API_URL = process.env.REACT_APP_N8N_WEBHOOK_URL || 'https://ijustknowthings.app.n8n.cloud/webhook/goldkey-assistant';

// Create axios instance with timeout and headers
const apiClient = axios.create({
  baseURL: API_URL,
  timeout: 30000, // 30 second timeout
  // Add cors mode and credentials
  withCredentials: false,
  headers: {
    'Content-Type': 'application/json',
    'Access-Control-Allow-Origin': '*'
  }
});

// Add request interceptor for logging
apiClient.interceptors.request.use(request => {
  console.log(`API Request to ${request.url}`);
  return request;
});

// Add response interceptor for error handling
apiClient.interceptors.response.use(
  response => response,
  error => {
    let errorMessage = 'An unknown error occurred.';
    
    if (error.response) {
      // The request was made and the server responded with an error status
      const status = error.response.status;
      if (status === 404) {
        errorMessage = 'The assistant service could not be found. Please check your connection.';
      } else if (status === 401 || status === 403) {
        errorMessage = 'Not authorized to access the assistant. Please log in again.';
      } else if (status >= 500) {
        errorMessage = 'The assistant service is currently experiencing issues. Please try again later.';
      } else {
        errorMessage = `Server error: ${error.response.data.message || error.message}`;
      }
    } else if (error.request) {
      // The request was made but no response was received
      errorMessage = 'Network error: Cannot reach the assistant service. Please check your internet connection.';
    }
    
    console.error('API Error:', errorMessage, error);
    return Promise.reject(new Error(errorMessage));
  }
);

/**
 * Send a message to the GoldKey AI Assistant via n8n webhook
 * 
 * @param {string} message - The user's query
 * @param {string} userName - The name of the staff member
 * @returns {Promise} - Promise containing the AI response
 */
export const sendMessage = async (message, userName) => {
  try {
    // Add retry logic for network issues
    let retries = 0;
    const maxRetries = 2;
    
    console.log(`Sending message to n8n webhook: ${API_URL}`, { message, userName });
    
    while (retries <= maxRetries) {
      try {
        const response = await apiClient.post('', {
          message,
          user: userName,
          timestamp: new Date().toISOString()
        }, {
          headers: {
            'Content-Type': 'application/json',
            'Access-Control-Allow-Origin': '*'
          },
          mode: 'cors'
        });
        
        console.log('Response from n8n webhook:', response.data);
        return response.data;
      } catch (error) {
        console.error(`Error attempt ${retries}:`, error);
        
        if (retries === maxRetries || !error.message.includes('Network error')) {
          throw error;
        }
        
        // Wait before retrying (exponential backoff)
        const delayMs = Math.pow(2, retries) * 1000;
        await new Promise(resolve => setTimeout(resolve, delayMs));
        retries++;
        console.log(`Retrying request (${retries}/${maxRetries})`);
      }
    }
  } catch (error) {
    console.error('Error sending message to AI assistant:', error);
    
    // Return graceful fallback response
    return {
      message: `I'm having trouble connecting to the assistant service. ${error.message}`,
      success: false,
      fallback: true
    };
  }
};

/**
 * Upload a document to be processed by the GoldKey AI Assistant
 * 
 * @param {File} file - The file to upload
 * @param {string} userName - The name of the staff member
 * @returns {Promise} - Promise containing the document analysis
 */
export const uploadDocument = async (file, userName) => {
  const formData = new FormData();
  formData.append('file', file);
  formData.append('user', userName);
  formData.append('timestamp', new Date().toISOString());
  
  try {
    const response = await apiClient.post('/upload', formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    });
    
    return response.data;
  } catch (error) {
    console.error('Error uploading document:', error);
    
    // Return graceful fallback for document upload errors
    return {
      message: `I couldn't process the document "${file.name}". ${error.message}`,
      success: false,
      fallback: true
    };
  }
};

/**
 * Test connection to the n8n webhook
 * 
 * @returns {Promise} - Promise containing the connection test result
 */
export const testConnection = async () => {
  try {
    console.log('Testing connection to API URL:', API_URL);
    
    // Just do a quick OPTIONS request to check if the endpoint is available
    const response = await fetch(API_URL, {
      method: 'OPTIONS',
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*'
      },
      mode: 'cors'
    });
    
    console.log('Connection test response:', response);
    
    return {
      success: response.ok,
      message: response.ok ? 'Connection to assistant service successful' : `Connection failed: ${response.status} ${response.statusText}`,
      details: {
        status: response.status,
        headers: Object.fromEntries([...response.headers])
      }
    };
  } catch (error) {
    console.error('Connection test error:', error);
    
    return {
      success: false,
      message: `Connection to assistant service failed: ${error.message}`,
      error
    };
  }
};

