/**
 * GoldKey Insurance Query Processor Utility
 * 
 * This utility provides advanced query processing capabilities for the
 * GoldKey Insurance Chat App, helping to extract entities, determine intent,
 * and provide structured data for queries to the n8n backend.
 */

/**
 * Insurance policy pattern matching
 * Recognizes common policy number formats like:
 * - GK-1234
 * - POL-5678
 * - HOM-1122-A
 * - AUTO-9988-B
 */
const POLICY_REGEX = /\b([A-Z]+-\d+(?:-[A-Z])?)\b/i;

/**
 * Claim number pattern matching
 * Recognizes formats like:
 * - C-1234
 * - CLM-5678
 * - CL-2022-1234
 */
const CLAIM_REGEX = /\b([C|CL|CLM]+-\d+(?:-\d+)?)\b/i;

/**
 * Date pattern matching
 * Recognizes various date formats
 */
const DATE_REGEX = /\b(0?[1-9]|1[0-2])[\/.-](0?[1-9]|[12]\d|3[01])[\/.-](19|20)?\d{2}\b/;

/**
 * Currency pattern matching
 * Recognizes dollar amounts with various formats
 */
const CURRENCY_REGEX = /\$\s?([0-9,]+(\.[0-9]{2})?)/;

/**
 * Common insurance terms for intent classification
 */
const INSURANCE_TERMS = {
  POLICY: ['policy', 'coverage', 'deductible', 'premium', 'insure', 'plan'],
  CLAIM: ['claim', 'damage', 'accident', 'incident', 'loss', 'file'],
  CLIENT: ['client', 'customer', 'policyholder', 'insured', 'member'],
  PAYMENT: ['payment', 'bill', 'invoice', 'pay', 'due', 'balance'],
  AGENT: ['agent', 'representative', 'broker', 'advisor'],
  QUOTE: ['quote', 'estimate', 'price', 'cost', 'rate']
};

/**
 * Extract policy numbers from a query string
 * 
 * @param {string} query - The user query
 * @returns {string|null} - Extracted policy number or null if not found
 */
export const extractPolicyNumber = (query) => {
  const match = query.match(POLICY_REGEX);
  return match ? match[1] : null;
};

/**
 * Extract claim numbers from a query string
 * 
 * @param {string} query - The user query
 * @returns {string|null} - Extracted claim number or null if not found
 */
export const extractClaimNumber = (query) => {
  const match = query.match(CLAIM_REGEX);
  return match ? match[1] : null;
};

/**
 * Extract client name from query
 * Looks for patterns like "client Johnson" or "customer Smith"
 * 
 * @param {string} query - The user query
 * @returns {string|null} - Extracted client name or null if not found
 */
export const extractClientName = (query) => {
  const clientTerms = INSURANCE_TERMS.CLIENT;
  const lowerQuery = query.toLowerCase();
  
  for (const term of clientTerms) {
    if (lowerQuery.includes(term)) {
      // Find term and try to extract the name that follows
      const termIndex = lowerQuery.indexOf(term);
      const afterTerm = lowerQuery.slice(termIndex + term.length).trim();
      
      // Look for a proper noun (capitalized word) after the client term
      const nameMatch = afterTerm.match(/\b([A-Z][a-z]+)\b/);
      if (nameMatch) {
        return nameMatch[1];
      }
      
      // If no capitalized name found, take the next word if it exists
      const wordMatch = afterTerm.match(/\b(\w+)\b/);
      if (wordMatch) {
        return wordMatch[1];
      }
    }
  }
  
  return null;
};

/**
 * Determine the most likely intent from a query
 * 
 * @param {string} query - The user query
 * @returns {string} - Intent category (POLICY, CLAIM, CLIENT, etc.)
 */
export const determineIntent = (query) => {
  const lowerQuery = query.toLowerCase();
  let scores = {};
  
  // Initialize scores for each category
  Object.keys(INSURANCE_TERMS).forEach(key => {
    scores[key] = 0;
  });
  
  // Calculate scores based on term occurrences
  Object.entries(INSURANCE_TERMS).forEach(([category, terms]) => {
    terms.forEach(term => {
      // Check for exact word match using word boundaries
      const regex = new RegExp(`\\b${term}\\b`, 'i');
      if (regex.test(lowerQuery)) {
        scores[category] += 1;
      }
    });
  });
  
  // Add extra points for detected entities
  if (extractPolicyNumber(query)) scores.POLICY += 2;
  if (extractClaimNumber(query)) scores.CLAIM += 2;
  if (extractClientName(query)) scores.CLIENT += 1;
  
  // Add points for question type
  if (lowerQuery.includes('what is the status')) scores.CLAIM += 1;
  if (lowerQuery.includes('how much') || lowerQuery.includes('cost')) scores.PAYMENT += 1;
  if (lowerQuery.includes('who is')) scores.CLIENT += 1;
  if (lowerQuery.includes('renew') || lowerQuery.includes('extend')) scores.POLICY += 1;
  
  // Find the highest scoring category
  let highestScore = 0;
  let mainIntent = 'GENERAL';
  
  Object.entries(scores).forEach(([category, score]) => {
    if (score > highestScore) {
      highestScore = score;
      mainIntent = category;
    }
  });
  
  return mainIntent;
};

/**
 * Extract all relevant entities from a query
 * 
 * @param {string} query - The user query
 * @returns {Object} - Object containing all extracted entities
 */
export const extractEntities = (query) => {
  return {
    policyNumber: extractPolicyNumber(query),
    claimNumber: extractClaimNumber(query),
    clientName: extractClientName(query),
    dates: query.match(DATE_REGEX) ? query.match(DATE_REGEX).map(d => d) : null,
    amounts: query.match(CURRENCY_REGEX) ? query.match(CURRENCY_REGEX).map(d => d[1]) : null
  };
};

/**
 * Process a query to get structured information for the n8n API
 * 
 * @param {string} query - The user query
 * @param {string} userName - The user's name
 * @returns {Object} - Structured query data for API
 */
export const processQuery = (query, userName) => {
  const intent = determineIntent(query);
  const entities = extractEntities(query);
  
  return {
    originalQuery: query,
    intent: intent,
    entities: entities,
    user: userName,
    timestamp: new Date().toISOString()
  };
};

/**
 * Generate suggested follow-up questions based on the current context
 * 
 * @param {string} currentQuery - The current query
 * @param {string} intent - The detected intent
 * @param {Object} entities - Extracted entities
 * @returns {Array<string>} - List of suggested follow-up questions
 */
export const generateFollowUpQuestions = (currentQuery, intent, entities) => {
  const suggestions = [];
  
  // Based on policy questions
  if (intent === 'POLICY' && entities.policyNumber) {
    suggestions.push(`What is the coverage amount for policy ${entities.policyNumber}?`);
    suggestions.push(`When does policy ${entities.policyNumber} expire?`);
    suggestions.push(`What is the deductible for policy ${entities.policyNumber}?`);
  }
  
  // Based on claim questions
  if (intent === 'CLAIM' && entities.claimNumber) {
    suggestions.push(`What is the current status of claim ${entities.claimNumber}?`);
    suggestions.push(`Who is the adjuster assigned to claim ${entities.claimNumber}?`);
    suggestions.push(`When will claim ${entities.claimNumber} be resolved?`);
  }
  
  // Based on client questions
  if (intent === 'CLIENT' && entities.clientName) {
    suggestions.push(`What policies does ${entities.clientName} currently have?`);
    suggestions.push(`Who is the agent for client ${entities.clientName}?`);
    suggestions.push(`When did ${entities.clientName} join GoldKey Insurance?`);
  }
  
  // General follow-ups if no specific entities
  if (suggestions.length === 0) {
    suggestions.push("Who is our newest client?");
    suggestions.push("What policies are expiring this month?");
    suggestions.push("Show me active claims awaiting adjuster assignment.");
  }
  
  return suggestions.slice(0, 3); // Return top 3 suggestions
};

export default {
  extractPolicyNumber,
  extractClaimNumber,
  extractClientName,
  determineIntent,
  extractEntities,
  processQuery,
  generateFollowUpQuestions
};
